import React,{useEffect} from "react";
import { useStores } from "../stores";
import InfiniteScroll from "react-infinite-scroller";
import { List, Spin } from "antd";
import styled from "styled-components";
import { observer } from "mobx-react";
import products from "../assets/product.json";
const Img = styled.img`
  width: 100px;
  height: 120px;
  object-fit: contain;
  border: 1px solid #eee;
`;
const Component = observer(() => {
  const { HistoryStore } = useStores();
  const loadMore = () => {
    HistoryStore.find("History");
  };

  useEffect(() => {
    return () => {
      HistoryStore.reset();
    }
  }, []);
  return (
    <div>
      <InfiniteScroll 
        initialLoad={true}
        pageStart={0}
        loadMore={loadMore}
        hasMore={!HistoryStore.isLoading && HistoryStore.hasMore}

      >
        <List
        dataSource={HistoryStore.list}
          renderItem={
              (item) => <List.Item key={item.id}>
              <div>
                <Img
                  height={120}
                  width={100}
                  src={products[item.attributes.type][item.attributes.No].photoAddress}
                />
              </div>
              <div>
              <h4>{products[item.attributes.type][item.attributes.No].name}
              </h4>
              </div>
              <div>
                   <h5>${products[item.attributes.type][item.attributes.No].price}
                   </h5>
                   
              </div>
              <div>
                   <h5>quantity : haimeizuo</h5>
              </div>
              <div>
              <h5>Time of purchase : {(item.createdAt).toString().split(" GMT+1000 (澳大利亚东部标准时间)")}</h5>
              </div>
            </List.Item>
          }
        >
          {HistoryStore.isLoading && HistoryStore.hasMore && (
            <div>
              <Spin  style={{marginLeft:"50%",transform:"translateX(-50%)"}} size={"large"} tip="loading" />
            </div>
          )}
        </List>
      </InfiniteScroll>

    </div>
  );
});

export default Component;
